import { select } from 'redux-saga/effects';
import { Capacitor } from '@capacitor/core';
import { Purchases } from '@awesome-cordova-plugins/purchases';

export default function* initPurchase() {
    const workspaceId = yield select((state) => state.workspace._id);
    const { _id: userId, email } = yield select((state) => state.currentUser.user);

    Purchases.setDebugLogsEnabled(process.env.NODE_ENV === 'development');
    Purchases.configureWith({
        apiKey: Capacitor.getPlatform() === 'ios' ?
            process.env.REACT_APP_REVENUE_PUBLIC_APPLE_API_KEY :
            process.env.REACT_APP_REVENUE_PUBLIC_GOOGLE_API_KEY,
        appUserID: `w_${workspaceId}`,
    });
    Purchases.setAttributes({
        workspaceId,
        userId,
        $email: email,
    });
}
