import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import useSWR from 'swr';
import pricesFetcher from './pricesFetcher';

export function useProductPrices() {
    const workspaceId = useSelector((state) => state.workspace._id);
    const { data } = useSWR(`api/workspaces/${workspaceId}/billing/products/prices`,
        pricesFetcher, { suspense: true });
    return data;
}

export function usePlanDetails(planId) {
    const { isCurrentPlan, subscription, currentPlanPeriod } = useSelector(({ billingDetails }) => {
        const isCurrentPlan = billingDetails.result?.subscription?.plan?.id === planId;
        const subscription = billingDetails.result?.subscription;

        return {
            subscription,
            isCurrentPlan,
            currentPlanPeriod: subscription?.period,
        };
    });
    const plans = useProductPrices();
    const plan = useMemo(() => plans?.find((plan) => plan.id === planId), [plans, planId]);

    return {
        plan,
        subscription,
        isCurrentPlan,
        currentPlanPeriod,
    };
}
